import { Component } from 'react'
import Section from "./Section"
import Color from "./Color"
import Headers from "./Headers"
import Paragraph from "./Paragraph"

import Menu from "./Menu"

class Container extends Component {
    constructor(props) {
        super(props)

        this.state = {
            font: 'sans-serif'
        }

        this.handleClick = this.handleClick.bind(this)
    }

    changeFont() {
        this.setState({
            font: this.state.font == 'serif' ? 
                'sans-serif' : 
                'serif'
        })
    }

    render() {
        return (
            <>
                <Menu handleClick={this.changeFont} />
                <Section title="01. Typography">
                    <Headers font={this.state.font}/>
                    <Paragraph font={this.state.font}/>
                </Section>
                <Section title="02. Color">
                    <Color colors={["white", "black-19", "black-39", "black-59", "black-79", "black-100"]} />
                    <Color colors={["primary-light", "primary", "primary-dark"]} />
                </Section>
            </>
        )
    }
}

export default Container